import React from 'react';
import PropTypes from 'prop-types';

import RetailPriceChartContainer from '../RetailPriceChart/RetailPriceChartContainer';
import InstrumentTableContainer from '../InstrumentTable/InstrumentTableContainer';
import InstrumentSelectorContainer from '../InstrumentSelector/InstrumentSelectorContainer';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import './MarketOverviewComponent.css';

const marketOverviewClasses = getBEMClasses('market-overview');

const MarketOverviewComponent = (props, context) => {
  return (
    <React.Fragment>
      <div className={marketOverviewClasses('header')}>
        {context.t('Markets Overview')}
      </div>
      <div className={marketOverviewClasses('instrument-selector')}>
        <InstrumentSelectorContainer
          customClass="market-overview-popup"
          showFullNameInstrument
          showTriangles
        />
      </div>
      <div className={marketOverviewClasses('chart-component-container')}>
        <RetailPriceChartContainer />
        <InstrumentTableContainer customClass="market-overview-instrument" />
      </div>
    </React.Fragment>
  );
};

MarketOverviewComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default MarketOverviewComponent;



